/***
**@Kernel {Class}
** 
**/
define('kernel/core',['note'],function(note){
	var w = window;
	w.inherit = function(proto){
		function F(){};
		F.prototype = proto;
		return new F;
	};
	w.extend = function(Child, Parent){
		Child.prototype = inherit(Parent.prototype);
		Child.prototype.constructor = Child;
		Child.parent = Parent.prototype;
	};
	Event.prototype.data = {};//
	w.fireEvent = w.dispatch = function(ta,type,data,bub){
		ta = ta || document;
		var evt = new Event(type,{bubbles:bub||true});
		evt.data = data || {name:"unknow"};
		ta.dispatchEvent(evt);
	}
	/***
	**@constructor {Class} Base
	**/	
	function Base(){};
	/**
	**@   
	**/
	Base.prototype._exchange = function(){}
	/**
	**@property {DOM poniter} _DOM
	**/
	Base.prototype._DOM = null;
	/**
	**@property {Array} _child   
	**/
	Base.prototype.children = [];	
	/**
	**@
	**/
	Base.prototype.init = function(){};
	/***
	**@
	**/
	Base.prototype.update = function(){ };
	/**
	**@
	**/
	Base.prototype.destory = function(){};		
	/**
	**@
	**/
	Base.prototype.reset = function(){};
	/**
	**@ 
	**/
	Base.prototype.notify = function(msg){
		this.children.forEach(function(child){
			if(child && child.hasOwnProperty('notify')) child.notify(msg)
		});	
	}
	/**
	**@constructor {class} Page    
	** represent the whole page   DOM document
	**  
	** page  piece 
	**/
	w.Page = function(){ arguments.callee._instance = this;};		
	extend(Page,Base);
	Page.notification  = {};
	Page.prototype.startup = function(){
		this.init();
		this.children.forEach(function(piece){
			piece.init();
		});		
	};
	
	Page.prototype.notify = function(msg){
		if(Object.observe){
			note.notification = msg;
		}else{
			Base.prototype.notify.call(this,msg);
		}
	}
	/**
	**@constructor {Class} Piece  
	**represent a piece of the Page, DOM 
	** header,footr,aside, article,body
	** piece   segment  
	** piece  
	**/
	w.Piece = function(){};
	extend(Piece,Base);
	/**
	**Piece  
	** 
	**/
	Piece.prototype.init = function(){
		var _this = this;
		this.note = note;
		this.children.forEach(function(child){//child = segment
			child.init();
		});
		if(Object.observe){
			Object.observe(note,this._react);
		}
	}
	/**
	**
	** 
	*/
	Piece.prototype.notify = function(msg){
		this._react(msg);	
		Base.prototype.notify.call(this,msg);
	}
	/**
	**
	**/
	Piece.prototype.adjust = function(){}
	/**
	** XHR 
	**@param {Notification Object} msg
	**/
	Piece.prototype._react = function(){}
	/**
	**@constructor {Class} Segment  
	**represent a part of Piece, 
	** segment combine  component 
	** segment 
	**/
	w.Segment = function(){};	
	extend(Segment,Piece);	
	/**
	**@constructor {Class} Combine 
	**  
	**/
	w.Combine = function(){};		
  	extend(Combine,Piece);		
});

